import { BasicColumn, FormSchema, useRender } from '@/components/Table';
import { defHttp } from '@/utils/http/axios';
import { useUserStore } from '/@/store/modules/user';

enum Api {
  getSafetyToolsScrapPage = '/po/safety-tools-scrap/page',
  // deleteSafetyToolsScrapData = '/po/safety-tools-scrap/delete',
  getSafetyToolsScrapData = '/po/safety-tools-scrap/get',
  exportSafetyToolsScrapData = '/po/safety-tools-scrap/export-excel',
}

// 查询
export function getSafetyToolsScrapPage(params) {
  return defHttp.get({ url: Api.getSafetyToolsScrapPage, params });
}
// 删除
// export function deleteSafetyToolsScrap(id: string) {
//   return defHttp.delete({ url: Api.deleteSafetyToolsScrapData + '?id=' + id });
// }
export function getSafetyToolsScrapData(params) {
  return defHttp.get({ url: Api.getSafetyToolsScrapData, params });
}
export function exportSafetyToolsScrapData(params) {
  return defHttp.download(
    { url: Api.exportSafetyToolsScrapData, params },
    { isReturnNativeResponse: true },
  );
}
const statusColor = {
  0: '#dc0000', // 草稿
  1: '#338ed1', // 处理中
  2: '#30ca5a', // 通过
  3: '#929292', // 不通过
  4: '#929292', // 已取消
  5: '#929292', // 已终止
  6: '#fda941', // 退回/驳回
};
